.header {
	&__top-line {
		padding: 8px 0;
		background-color: #22273b;
		@include sm {
			padding: 10px 0 6px; }
		@include xs {
			padding: 5px 0; } }
	&__container {
		@include clr; }
	&__nav {
		float: left; }
	&__screen {
		position: relative;
		min-height: 810px;
		padding: 50px 0 40px;
		@include lg {
			min-height: 700px;
			padding: 35px 0 40px; }
		@include sm {
			min-height: 660px;
			padding: 40px 0 15px; }
		@include xs {
			padding: 35px 0 15px; }
		&::before {
			pointer-events: none;
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			height: 200px;
			width: 100%;
			z-index: 1;
			background-image: linear-gradient(0deg, transparent 0, rgba(0, 0, 0, 0.55) 45%, rgba(0, 0, 0, 0.45) 100%);
			@include lg {
				height: 120px;
				background-image: linear-gradient(0deg, transparent 0, rgba(0, 0, 0, 0.45) 38%, rgba(0, 0, 0, 0.45) 100%); } }
		&_has-label::after {
			content: '';
			position: absolute;
			right: 17%;
			bottom: -60px;
			z-index: 1;
			background: url(../img/bg/best-label.png) no-repeat 50% 50%/cover;
			width: 146px;
			height: 222px;
			@include lg {
				right: 6%;
				bottom: -75px; }
			@include md {
				right: 1%;
				bottom: -90px; }
			@include sm {
				display: none; } }
		&-container {
			position: relative;
			z-index: 2;
			@include clr;
			@include xs {
				display: flex;
				flex-direction: column;
				&::after {
					content: none; } } }
		&-pic {
			@include cover;
			@include coverdiv;
			z-index: -1; }

		&-title {
			padding: 85px 10px 0;
			color: #fff;
			text-transform: uppercase;
			text-align: center;
			line-height: 1.3;
			@include f('cb', 41);
			text-shadow: 0 1px 2px rgba(0, 2, 1, 0.55);
			&_lg {
				font-size: 57px;
				text-transform: none;
				line-height: 1;
				@include md {
					font-size: 49px; }
				@include sm {
					padding: 45px 10px 0; }
				@include xs {
					padding: 40px 0 0;
					font-size: 31px; } } }
		&_sm {
			min-height: 605px;
			@include lg {
				min-height: calc(100vh - 49px); }
			@include sm {
				min-height: 480px; }
			@include xs {
				min-height: 420px; } } }

	&__logo {
		float: left;
		@include xs {
			float: none;
			order: 1;
			align-self: flex-start; } }
	&__menu {
		float: left;
		margin: 11px 0 0 30px;
		@include xs {
			order: 3; } }
	&__call-me {
		float: left;
		margin-left: 30px;
		@include lg {
			padding-left: 65px; }
		@include md {
			padding-left: 0;
			margin-left: 15px; }
		@include r(919) {
			margin-top: -12px; }
		@include sm {
			text-align: right; }
		@include xs {
			margin: -60px 0 0;
			align-self: flex-end;
			order: 2; } }
	&__pick-btn {
		float: right;
		@include lg {
			padding: 15px 31px; }
		@include md {
			margin-top: 3px;
			padding: 10px 17px;
			font-size: 16px; }
		@include sm {
			margin-top: 10px;
			padding: 7px 21px;
			font-size: 15px; }
		@include xs {
			float: none;
			order: 2;
			align-self: flex-end; } } }

/* nav
	----------------------------------- */
.nav {
	@include xs {
		display: flex;
		justify-content: center;
		align-items: center;
		position: fixed;
		min-width: 320px;
		top: 51px;
		right: 0;
		left: 0;
		bottom: 0;
		padding: 0;
		z-index: 10;
		background-color: #22273b;
		opacity: 0;
		pointer-events: none;
		transform: translateX(-100%);
		transition: opacity .5s, transform .5s;
		overflow: auto;
		&.is-active {
			opacity: 1;
			pointer-events: all;
			transform: translateX(0);
			.nav__list-item {
				transform: translateX(0);
				opacity: 1; }
			.nav__list-item::after {
				transform: translateX(0); } } }
	&__list {
		font-size: 0;
		@include xs {
			display: flex;
			justify-content: space-between;
			flex-basis: 100%;
			align-items: stretch;
			flex-direction: column;
			overflow: auto;
			padding: 0;
			text-align: center; }
		&-item {
			display: inline-block;
			vertical-align: middle;
			&.is-active .nav__link {
				color: $blue; }
			& + & {
				margin-left: 46px;
				@include md {
					margin-left: 25px; }
				@include sm {
					margin-left: 11px; }
				@include xs {
					margin-left: 0; } }
			@include xs {
				position: relative;
				transition: transform .3s ease-in, opacity .3s;
				opacity: 0;
				transform: translateX(-100%);
				transition-delay: .2s;
				&:not(:last-child) {
					margin-bottom: 20px; }
				&::after {
					transform: translateX(-200%);
					width: 100%;
					content: '';
					border-bottom: 1px solid rgba($blue, .5);
					position: absolute;
					left: 0;
					top: 50%;
					z-index: 2;
					transition-delay: 3.5s;
					transition: transform 1.5s ease-in;
					pointer-events: none; } } }
		&-link {
			font-size: 16px;
			color: $white;
			@include tr(color .15s ease-in);
			@include sm {
				font-size: 14px; }
			@include xs {
				position: relative;
				z-index: 3;
				padding: 0 20px;
				background-color: #22273B;
				@include f('cl', 22); }
			&_main {
				@include f('cb', 24);
				@include sm {
					font-size: 18px; } }
			&:hover {
				color: $blue; } } } }

/* menu
	----------------------------------- */
.menu {
	position: relative;
	@include md {
		margin: 11px 0 0 10px; }
	@include xs {
		margin: 15px 0 0;
		width: 100%; }
	@include r(439) {
		display: none; }
	&::before {
		content: '';
		position: absolute;
		left: -250%;
		bottom: -54px;
		z-index: 1;
		width: 700%;
		height: 1px;
		background-color: rgba(#fff, .1);
		pointer-events: none;
		@include sm {
			bottom: -51px; }
		@include xs {
			bottom: -19px; } }
	&__list {
		font-size: 0;
		@include xs {
			display: flex;
			justify-content: space-between; }
		&-item {
			position: relative;
			padding: 0 12px;
			display: inline-block;
			vertical-align: top;
			@include sm {
				padding: 0 5px; }
			@include xs {
				padding: 0; } }
		&-link {
			position: relative;
			display: block;
			@include f('cb', 18);
			line-height: 1.15;
			color: $white;
			border-bottom: 1px solid rgba($white, .8);
			@include lg {
				font-size: 16px; }
			@include sm {
				font-size: 15px; } } }
	&__line {
		position: absolute;
		bottom: -53px;
		z-index: 1;
		height: 5px;
		transition: transform .25s linear, width .25s ease-in, opacity .4s;
		background-color: $blue;
		opacity: 0;
		pointer-events: none;
		@include sm {
			bottom: -50px; }
		@include xs {
			bottom: -19px; }
		&.is-init {
			opacity: 1; } } }

/* call-me
	----------------------------------- */
.call-me {
	position: relative;
	padding-left: 75px;
	color: $white;
	&::before {
		position: absolute;
		top: 3px;
		left: 3px;
		@include s('phone');
		content: '';
		@include md {
			display: none; } }
	&__number {
		display: block;
		font-size: 20px;
		line-height: 1;
		@include lg {
			font-size: 18px; }
		@include sm {
			font-size: 16px; } }
	&__btn {
		font-size: 15px;
		color: inherit;
		background-color: transparent;
		span {
			margin-top: 5px;
			padding-bottom: 1px;
			background-image: linear-gradient(to right, rgba(#fff, .4) 50%, transparent 50%);
			background-position: 100% 100%;
			background-size: 7px 1px; }
		&:hover {
			background-image: none;
			color: $white; } } }
