.why-us {
	background-color: #fff;
	&_default {
		padding: 80px 0 30px;
		@include md {
			padding: 40px 0 20px; } }
	&_entry {
		padding: 63px 0 25px;
		@include sm {
			padding: 40px 0 25px; }
		@include xs {
			padding: 20px 0; }
		.title-md {
			@include f('cr'); } }
	&__container {
		max-width: 1220px;
		padding: 75px 15px 0;
		margin: 0 auto;
		@include md {
			padding: 50px 15px 0; }
		@include sm {
			padding: 30px 15px 0; } }
	&__row {
		font-size: 0;
		margin: 0 -15px; }
	&__item {
		display: inline-block;
		vertical-align: top;
		width: 33.33333%;
		padding: 0 15px;
		margin-bottom: 35px;
		@include sm {
			margin-bottom: 20px;
			padding: 0 10px; }
		@include xs {
			width: 50%; }
		@include r(424) {
			width: 100%; }
		&-pic {
			position: relative;
			display: block;
			max-width: 230px;
			margin: 0 auto;
			overflow: hidden;
			z-index: 1;
			&:hover::after {
				transform: rotate(50deg) translate(-420%, -30%); }
			&::after {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				z-index: 2;
				transform: rotate(55deg) translate(520%, -40%);
				width: 40px;
				height: 145%;
				transition: transform 0.33s ease-in;
				background: rgba(#fff, .4);
				will-change: transform;
				pointer-events: none; }
			@include sm {
				max-width: 180px; } }
		&-img {
			width: 100%;
			height: 100%; }
		&-text {
			max-width: 292px;
			margin: 0 auto;
			text-align: center;
			@include f('cb', 20);
			line-height: 1.2;
			@include sm {
				font-size: 18px; }
			small {
				font-size: 16px; } } } }
