.selectric-wrapper {
	position: relative;
	cursor: pointer; }

.selectric-responsive {
	width: 100%; }

.selectric {
	display: flex;
	align-items: center;
	height: 46px;
	border-bottom-width: 2px;
	background: #fff;
	position: relative;
	border-radius: 4px;
	.label {
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		margin: 0 30px 0 0;
		padding: 6px 6px 6px 15px;
		font-size: 15px;
		line-height: 18px;
		color: #1f1f1f;
		min-height: 18px; }
	.button {
		display: block;
		position: absolute;
		right: 10px;
		top: calc(50% - 3px);
		width: 10px;
		height: 6px;
		background: url(data:image/svg+xml;base64,) repeat 50% 50%/cover;
		font: 0/0 a; } }

@media \0screen\,screen\9 {
	.selectric .button {
		color: #DDD;
		text-align: center;
		font: 20px/30px Lucida Sans Unicode, Arial Unicode MS, Arial; } }

.selectric-focus .selectric {
	border-color: #AAA; }

.selectric-hover .selectric {
	border-color: #CCC;
	.button {
		color: #888;
		&:after {
			border-top-color: #888; } } }

.selectric-open {
	z-index: 9999;
	.selectric {
		border-color: #CCC;
		background: #F0F0F0;
		border-bottom-width: 1px; }
	.selectric-items {
		visibility: visible;
		opacity: 1;
		text-align: left;
		-webkit-transform: translateY(0);
		-ms-transform: translateY(0);
		-o-transform: translateY(0);
		transform: translateY(0);
		-webkit-transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
		transition: 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275); } }

.selectric-disabled {
	filter: alpha(opacity = 50);
	opacity: 0.5;
	cursor: default;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none; }

.selectric-hide-select {
	position: relative;
	overflow: hidden;
	width: 0;
	height: 0;
	select {
		position: absolute;
		left: -100%; }
	&.selectric-is-native {
		position: absolute;
		width: 100%;
		height: 100%;
		z-index: 10;
		select {
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			height: 100%;
			width: 100%;
			border: none;
			z-index: 1;
			box-sizing: border-box;
			opacity: 0; } } }

.selectric-input {
	position: absolute !important;
	top: 0 !important;
	left: 0 !important;
	overflow: hidden !important;
	clip: rect(0, 0, 0, 0) !important;
	margin: 0 !important;
	padding: 0 !important;
	width: 1px !important;
	height: 1px !important;
	outline: none !important;
	border: none !important;
	*font: 0/0 a !important;
	background: none !important; }

.selectric-temp-show {
	position: absolute !important;
	visibility: hidden !important;
	display: block !important; }

/* Items box */

.selectric-items {
	visibility: hidden;
	opacity: 0;
	-webkit-transform: translateY(-20px);
	-ms-transform: translateY(-20px);
	-o-transform: translateY(-20px);
	transform: translateY(-20px);
	position: absolute;
	top: 100%;
	left: 0;
	background: #fefefe;
	border: 1px solid #e9e9e9;
	z-index: -1;
	box-shadow: 0 0 10px -6px, inset 0 0 0px 1px #fff;
	margin: 8px 0;
	padding: 10px 0;
	border-radius: 4px;
	.selectric-scroll {
		height: 100%;
		overflow: auto; }
	&:before {
		content: "";
		position: absolute;
		top: -9px;
		left: 12px;
		width: 0;
		height: 0;
		border-bottom: 6px solid #BBB;
		border-left: 6px solid transparent;
		border-right: 6px solid transparent;
		margin-top: 3px; }
	&:after {
		content: "";
		position: absolute;
		top: -9px;
		left: 12px;
		width: 0;
		height: 0;
		border-bottom: 6px solid #BBB;
		border-left: 6px solid transparent;
		border-right: 6px solid transparent;
		margin-top: 3px;
		margin-top: 4px;
		border-bottom-color: #FFF; } }

.selectric-above .selectric-items {
	&:before {
		top: 100%;
		border-bottom-width: 0;
		border-top: 6px solid #BBB;
		margin-top: 0; }
	&:after {
		top: 100%;
		border-bottom-width: 0;
		border-top: 6px solid #BBB;
		margin-top: 0;
		border-top-color: #FFF;
		margin-top: -1px; }
	top: auto;
	bottom: 100%; }

.selectric-items {
	ul {
		list-style: none;
		padding: 0;
		margin: 0;
		font-size: 12px;
		line-height: 20px;
		min-height: 20px; }
	li {
		list-style: none;
		margin: 0;
		line-height: 20px;
		min-height: 20px;
		display: block;
		padding: 6px 30px 6px 10px;
		cursor: pointer;
		position: relative;
		overflow: hidden;
		font-size: 15px;
		color: #1f1f1f;
		&.selected {
			background: #0590ff;
			color: #fff; }
		&.highlighted {
			background: #0590ff;
			color: #fff; }
		&:after {
			content: "";
			position: absolute;
			z-index: 3;
			top: 0;
			right: -10px;
			width: 30px;
			height: 100%;
			background: url(data:image/svg+xml;base64,) no-repeat 50% 50%;
			opacity: 0;
			-webkit-transition: .2s;
			transition: .2s; }
		&.selected:after {
			opacity: 1;
			right: 0; }
		&:hover {
			background: #dedede;
			color: #1f1f1f; } }
	.disabled {
		filter: alpha(opacity = 50);
		opacity: 0.5;
		cursor: default !important;
		background: none !important;
		color: #666 !important;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none; }
	.selectric-group {
		.selectric-group-label {
			font-weight: bold;
			padding-left: 10px;
			cursor: default;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
			background: none;
			color: #1f1f1f; }
		&.disabled li {
			filter: alpha(opacity = 100);
			opacity: 1; }
		li {
			padding-left: 25px; } } }
