.features {
	&_default {
		padding: 40px 0;
		@include sm {
			padding: 30px 0 20px; } }
	&__title {
		margin-bottom: 70px;
		@include md {
			margin-bottom: 40px; }
		@include sm {
			margin-bottom: 25px; } }
	&__row {
		font-size: 0; }
	&__item {
		width: 25%;
		display: inline-block;
		vertical-align: top;
		padding: 0 15px;
		text-align: center;
		@include sm {
			padding: 0 10px; }
		@include xs {
			width: 50%; }
		@include r(424) {
			width: 100%; }
		&-pic {
			height: 100px;
			margin-bottom: 12px;
			@include vertical;
			@include sm {
				transform: scale(.8);
				margin-bottom: 0; }
			&::after {
				display: inline-block;
				vertical-align: middle;
				margin: 0 auto;
				content: ''; } }
		&-pic_modern::after {
			@include s('modern'); }
		&-pic_refrigerator::after {
			@include s('refrigerator'); }
		&-pic_repair::after {
			@include s('repair'); }
		&-pic_ship::after {
			@include s('ship'); }
		&-descr {
			max-width: 215px;
			margin: 0 auto;
			font-size: 18px;
			line-height: 1.3;
			@include lg {
				font-size: 17px; }
			@include sm {
				font-size: 15px; }
			strong {
				display: block;
				@include f('cb'); } } } }
