$icons: (0:0);
$icons: map-merge($icons,('calendar': (X: -228px, Y:-51px, W: 26px, H: 26px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('call': (X: -228px, Y:-87px, W: 16px, H: 16px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('modern': (X: -114px, Y:0px, W: 104px, H: 91px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('phone': (X: -228px, Y:0px, W: 41px, H: 41px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('refrigerator': (X: 0px, Y:0px, W: 104px, H: 95px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('repair': (X: 0px, Y:-105px, W: 104px, H: 91px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('ship': (X: -114px, Y:-105px, W: 104px, H: 79px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('time': (X: -254px, Y:-87px, W: 15px, H: 15px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('user': (X: -228px, Y:-113px, W: 14px, H: 16px, TW: 269px, TH: 196px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }


