.choice {
	&-slider {
		display: none;
		margin-top: 45px;
		@include xs {
			margin-top: 20px; }
		&.slick-initialized {
			display: block; }
		.slick-dots {
			position: absolute;
			left: 59.3%;
			bottom: 10px;
			z-index: 2;
			font-size: 0;
			@include xs {
				bottom: 15px;
				left: 50%;
				transform: translateX(-50%); }
			li {
				display: inline-block;
				vertical-align: middle;
				&:not(:last-child) {
					margin-right: 4px; }
				&.slick-active button {
					background: #0590ff; } }
			button {
				width: 13px;
				height: 13px;
				border-radius: 50%;
				background: $white;
				@include hide-text; } }
		&__btn {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			background: transparent;
			z-index: 2;
			font-size: 0;
			@include xs {
				top: 270px; }
			&_prev {
				left: 20px;
				@include sm {
					left: 7px; }
				@include xs {
					left: 3px; } }
			&_next {
				right: 20px;
				@include sm {
					right: 7px; }
				@include xs {
					right: 3px; } }
			&-icon {
				fill: $white;
				font-size: 23px; } }
		&__item {
			position: relative;
			&-pic {
				padding-top: percentage(405 / 1256);
				@include cover;
				@include xs {
					padding: 0;
					height: 220px; } }
			&-text {
				position: absolute;
				top: 0;
				left: 0;
				right: 50.2%;
				bottom: 0;
				z-index: 1;
				padding: 63px 55px 20px 120px;
				color: $white;
				background-color: rgba(#0590ff, .8);
				@include md {
					padding: 25px 25px 20px 70px; }
				@include sm {
					padding: 10px 15px 10px 40px; }
				@include xs {
					position: static;
					padding: 20px 35px 40px; } }
			&-title {
				margin-bottom: 5px;
				font-size: 39px;
				@include sm {
					margin-bottom: 0;
					font-size: 28px; } }
			&-descr {
				font-size: 16px;
				line-height: (30/16);
				@include md {
					font-size: 15px;
					line-height: 1.5; }
				@include sm {
					line-height: 1.25; } } } }
	&_default {
		padding: 90px 0;
		@include sm {
			padding: 50px 0 65px; }
		@include xs {
			padding:  35px 0 45px; } } }
