.team {
	&__container {
		padding: 35px 15px 0;
		font-size: 0;
		@include sm {
			display: none;
			max-width: 790px;
			padding: 25px 30px 0;
			&.slick-initialized {
				display: block; } }
		@include xs {
			max-width: 320px; }
		.slick-track {
			display: flex; } }
	&__person {
		display: inline-block;
		vertical-align: bottom;
		width: percentage(230 / 1256);
		margin: 0 percentage(5 / 1256);
		@include md {
			width: 17.8121%; }
		@include sm {
			margin: 0 3px; }
		&_lg {
			width: percentage(286 / 1256);
			@include md {
				width: 24.7707%; }
			@include sm {
				margin: 0 3px;
				width: 33%; } }
		&:first-child {
			margin-right: percentage(10 / 1256);
			margin-left: 0;
			@include sm {
				margin: 0 3px;
				order: 1; }
			@include xs {
				order: 1; } }
		&:nth-child(2) {
			@include xs {
				order: 3; } }
		&:last-child {
			margin-left: percentage(10 / 1256);
			margin-right: 0;
			@include sm {
				margin: 0 3px; } }
		&-img-wrap {
			position: relative;
			padding-top: 150%; }
		&-img {
			@include coverdiv;
			@include cover; }
		&-text {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;
			min-height: 60px;
			padding: 15px 5px 0;
			text-align: center;
			p {
				@include f('cb', 18);
				line-height: 1.2;
				color: #000;
				@include md {
					font-size: 16px; } } } }

	&__slider-btn {
		position: absolute;
		top: 5.6%;
		bottom: 13.9%;
		font-size: 0;
		background: transparent;
		z-index: 10;
		&_prev {
			left: 3px; }
		&_next {
			right: 3px; } }
	&__slider-btn-icon {
		font-size: 26px;
		fill: #000; }
	&_default {
		padding:  30px 0 112px;
		@include md {
			padding: 15px 0 55px; }
		@include xs {
			padding: 15px 0; } } }
