.contact {
	background: #fff;
	&__title {
		text-align: center;
		@include md {
			font-size: 30px; }
		@include sm {
			font-size: 28px; }
		@include xs {
			font-size: 25px; }
		&_upper {
			text-transform: uppercase;
			font-size: 24px;
			@include md {
				font-size: 22px; }
			@include sm {
				font-size: 20px; }
			@include xs {
				font-size: 14px; } }
		&_color {
			color: $grey; } }
	&__descr {
		max-width: 890px;
		margin: 0 auto 30px;
		font-size: 20px;
		line-height: (26/20);
		color: $white;
		text-align: center;
		@include lg {
			font-size: 19px; }
		@include md {
			margin: 0 auto 20px;
			font-size: 17px; }
		@include sm {
			font-size: 16px; } }
	&__form-in {
		margin: 30px auto 0;
		max-width: 884px;
		font-size: 0;
		text-align: center;
		@include justify;
		@include xs {
			margin: 20px auto 0;
			text-align: center; }
		&_sm {
			max-width: 660px; }
		&_sm .contact__form-field {
			width: percentage(210 / 660);
			@include xs {
				width: 80%; } } }
	&__form-field {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		width: percentage(210 / 884);
		@include xs {
			width: 80%;
			&:not(:last-child) {
				margin-bottom: 10px; } }
		.icon {
			position: absolute;
			top: 12px;
			left: 11px;
			@include sm {
			    top: 9px; } }
		&_border {
			border: 1px solid #acacac;
			border-radius: 3px; } }
	&__form-input {
		padding: 8px 10px 8px 38px;
		@include sm {
			padding: 6px 10px 6px 38px; } }
	&__form-btn {
		min-height: 39px;
		width: 100%;
		text-align-last: center;
		@include f('cb', 18);
		@include sm {
			min-height: 0;
			padding: 4px 10px 4px 10px; } }

	&_default {
		padding: 35px 0;
		@include sm {
			padding: 15px 0; } } }


