body {
	min-width: 320px;
	@include f('cr', 18);
	@include smooth-font;
	line-height: 1.4;
	color: $grey;
	&.is-locked {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		height: 100%;
		overflow: hidden; }
	@include lg {
		font-size: 17px; }
	@include sm {
		font-size: 16px; } }

h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

a {
	color: inherit;
	text-decoration: none; }

img {
	display: block; }

strong {
	@include f('cb'); }

// basic transition
@mixin trs {
	transition: .25s ease-out; }

.out {
	overflow: hidden; }

.container {
	max-width: 1286px;
	margin: 0 auto;
	padding: 0 15px;
	@include sm {
		padding: 0 10px; } }

.btn {
	display: inline-block;
	padding: 15px 43px;
	border-radius: 3px;
	background: $blue;
	@include f('cb', 18);
	color: #fff;
	line-height: 1.1;
	@include tr(background .25s ease-out);
	&:hover {
		background: darken($blue, 8); } }

.btn-transparent {
	display: inline-block;
	padding: 6px 10px 6px 10px;
	background-color: transparent;
	@include f('cb', 16);
	color: $white;
	border: 2px solid $white;
	border-radius: 3px;
	@include trs;
	&_border {
		color: #000;
		border-radius: 3px;
		border: 2px solid $blue; }
	&_blue {
		background-color: #0590ff;
		border: none;
		&:hover {
			background-color: darken(#0590ff, 10); } } }

.title-sm {
	@include f('cb', 32);
	line-height: 1.4;
	color: $white;
	&_color {
		color: $grey; } }

.title-md {
	@include f('cb', 40);
	text-transform: uppercase;
	color: $grey;
	text-align: center;
	@include lg {
		font-size: 38px; }
	@include sm {
		font-size: 30px; }
	@include xs {
		font-size: 20px; }
	&_norm {
		text-transform: none; }

	&_font {
		text-transform: none;
		@include f('cr'); } }

.title-lg {
	@include f('cb', 57);
	color: $white; }

.link-decor {
	display: inline-block;
	vertical-align: middle;
	color: $grey;
	background: transparent;
	&:hover {
		color: $blue; }
	&:hover span {
		background-image: linear-gradient(to right, $blue 50%, transparent 50%); }
	span {
		display: inline-block;
		vertical-align: middle;
		padding-bottom: 2px;
		background-color: transparent;
		background-image: linear-gradient(to right, rgba($grey, .6) 50%, transparent 50%);
		background-position: 100% 100%;
		background-size: 9px 1px;
		background-repeat: repeat-x;
		@include sm {
			padding-bottom: 0; } }
	&_has-icon {
		span {
			margin-left: 10px;
			@include xs {
				margin-left: 5px; } }
		.icon {
			@include xs {
				transform: scale(.9); } } } }

.input {
	width: 100%;
	padding: 8px 10px;
	@include f('cr', 16);
	color: $grey;
	@include placeholder-color(#a8a8a8);
	border-radius: 3px; }

.icon {
	display: inline-block;
	vertical-align: middle; }
